"use client";
import { useState, useRef } from 'react';

export function useGeminiLive() {
    const [status, setStatus] = useState<"Idle" | "Listening" | "Analyzing">("Idle");
    const [lastResponse, setLastResponse] = useState("");
    const videoRef = useRef<HTMLVideoElement>(null);
    const canvasRef = useRef<HTMLCanvasElement>(null);

    const askExpert = async (userQuestion: string, customBase64?: string) => {
        // ÿßŸÑÿ£ŸàŸÑŸàŸäÿ© ÿßŸÑŸÇÿµŸàŸâ ŸÑŸÑÿµŸàÿ±ÿ© ÿßŸÑŸÖÿ±ŸÅŸàÿπÿ© ŸäÿØŸàŸäÿßŸã ŸÑÿ∂ŸÖÿßŸÜ ÿπÿØŸÖ ÿ™ÿ≠ŸÑŸäŸÑ "Ÿàÿ¨Ÿá ÿßŸÑŸÅŸÜŸä"
        let finalImage = customBase64;

        if (!finalImage) {
            if (!videoRef.current || !canvasRef.current) return;
            const context = canvasRef.current.getContext("2d");
            context?.drawImage(videoRef.current, 0, 0, 720, 1280);
            finalImage = canvasRef.current.toDataURL("image/jpeg").split(",")[1];
        }

        setStatus("Analyzing");
        try {
            const res = await fetch(`https://generativelanguage.googleapis.com/v1beta/models/gemini-2.5-flash:generateContent?key=${process.env.NEXT_PUBLIC_GEMINI_API_KEY}`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({
                    contents: [{
                        parts: [
                            {
                                text: `STRICT SYSTEM INSTRUCTION: 
                                     ÿ£ŸÜÿ™ ŸÖÿ≥ÿßÿπÿØ ŸÅŸÜŸä ÿÆÿ®Ÿäÿ± ŸÅŸä ÿπÿØÿßÿØÿßÿ™ ÿ¥ÿ±ŸÉÿ© ÿßŸÑŸÉŸáÿ±ÿ®ÿßÿ° ÿßŸÑÿ≥ÿπŸàÿØŸäÿ©. 
                                     ŸÖŸáŸÖÿ™ŸÉ ŸÅÿ≠ÿµ ÿßŸÑÿµŸàÿ±ÿ© ÿßŸÑŸÖÿ±ŸÅŸÇÿ© (ÿ™ŸÜÿ≤ŸäŸÑ.png) ÿ®ÿØŸÇÿ© ŸÖÿ™ŸÜÿßŸáŸäÿ©.
                                     1. ÿßÿ≥ÿ™ÿÆÿ±ÿ¨ ÿßŸÑÿ±ŸÇŸÖ ÿßŸÑŸÖŸÉÿ™Ÿàÿ® ŸäÿØŸàŸäÿßŸã ŸÅŸä ÿßŸÑÿ£ÿπŸÑŸâ (Ÿäÿ¨ÿ® ÿ£ŸÜ ÿ™ÿ¨ÿØ 104679).
                                     2. ÿ≠ŸÑŸÑ ÿ≠ÿßŸÑÿ© ÿßŸÑÿ£ÿ≥ŸÑÿßŸÉ ÿßŸÑÿ´ŸÑÿßÿ´ÿ© (ÿ£ÿ≠ŸÖÿ±ÿå ÿ£ÿµŸÅÿ±ÿå ÿ£ÿ≤ÿ±ŸÇ) ÿßŸÑÿØÿßÿÆŸÑÿ© ŸÅŸä ÿßŸÑŸÇÿßÿ∑ÿπ ÿßŸÑÿ≥ŸÅŸÑŸä (MCCB).
                                     3. ÿßŸÅÿ≠ÿµ ÿ≠ÿßŸÑÿ© ŸÖŸÅÿ™ÿßÿ≠ ÿßŸÑŸÇÿßÿ∑ÿπ ŸàÿßŸÑŸÑŸÖÿ®ÿßÿ™.
                                     4. ÿ£ÿ¨ÿ® ÿ®ÿ™ŸÇÿ±Ÿäÿ± ŸÅŸÜŸä ŸÜŸÇÿßÿ∑Ÿä ÿ®ŸÑŸáÿ¨ÿ© ÿ≥ÿπŸàÿØŸäÿ© ÿ±ÿ≥ŸÖŸäÿ© Ÿàÿµÿßÿ±ŸÖÿ©. 
                                     ŸÖŸÖŸÜŸàÿπ ÿßŸÑÿ™ÿ≠Ÿäÿ©ÿå ÿßÿ®ÿØÿ£ ÿ®ÿßŸÑŸÖŸÑÿßÿ≠ÿ∏ÿßÿ™ ŸÅŸàÿ±ÿßŸã.` },
                            { inline_data: { mime_type: "image/jpeg", data: finalImage } }
                        ]
                    }],
                    generationConfig: { temperature: 0.1, maxOutputTokens: 1000 }
                })
            });

            const data = await res.json();
            const text = data.candidates?.[0]?.content?.parts?.[0]?.text;

            if (text) {
                setLastResponse(text);
                window.speechSynthesis.cancel();
                const speech = new SpeechSynthesisUtterance(text);
                speech.lang = 'ar-SA';
                window.speechSynthesis.speak(speech);
            }
        } catch (e) {
            setLastResponse("ÿ™ÿπÿ∞ÿ± ÿ™ÿ≠ŸÑŸäŸÑ ÿßŸÑÿµŸàÿ±ÿ© ŸÅŸÜŸäŸëÿßŸã. ÿ™ÿ£ŸÉÿØ ŸÖŸÜ Ÿàÿ∂Ÿàÿ≠ ÿßŸÑÿ£ÿ≥ŸÑÿßŸÉ.");
        }
        setStatus("Idle");
    };

    const analyzeImage = (file: File) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onloadend = () => {
            const base64 = (reader.result as string).split(",")[1];
            // ÿ™ŸÖÿ±Ÿäÿ± ÿßŸÑŸÅÿßÿµŸÑÿ© ÿßŸÑÿ®ÿ±ŸÖÿ¨Ÿäÿ© (,) ŸÑÿ∂ŸÖÿßŸÜ ÿπÿØŸÖ ÿ≠ÿØŸàÿ´ Build Error
            askExpert("ÿßŸÅÿ≠ÿµ ŸÑŸä ÿ≠ÿßŸÑÿ© ÿßŸÑÿ™ŸàÿµŸäŸÑÿßÿ™ ŸàÿßŸÑÿ±ŸÇŸÖ ÿßŸÑŸäÿØŸàŸä ŸàÿßŸÑÿπÿØÿßÿØ", base64);
        };
    };

    const startListening = () => {
        const Recognition = (window as any).webkitSpeechRecognition;
        if (!Recognition) return alert("ÿßŸÑŸÖÿ™ÿµŸÅÿ≠ ŸÑÿß ŸäÿØÿπŸÖ ÿßŸÑÿµŸàÿ™");
        const rec = new Recognition();
        rec.lang = 'ar-SA';
        rec.onresult = (e: any) => askExpert(e.results[0][0].transcript);
        rec.start();
        setStatus("Listening");
    };

    return { videoRef, canvasRef, status, startListening, lastResponse, analyzeImage };
}